(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log, $rootScope, $scope, $uibModal, $location, $window, security) {
    var vm = this;
    var isLogined;

    /*
    高亮显示标签页
     */
    $scope.isActive = vm.isActive = function(path) {
      var pathLen = 0;
      var locationPath;

      if (path === '') return false;

      pathLen = path.length;
      locationPath = $location.path().substr(-pathLen, pathLen);

      return path === locationPath;
    };

    /*
    当路由发生改变时，判别是否已登录，若非登录则跳转至登录页。
     */
    $rootScope.$on('$stateChangeStart',
      function(event, toState, toParams, fromState, fromParams, options) {
        isLogined = security.getFromSession('isLogined');
        if (toState.name === 'login') {
          if (isLogined) {
            event.preventDefault();
          }
        }
      });
  }
})();

